.navbar{
    grid-area: n;
    background: linear-gradient(to bottom, #FAFAFA, #E8F1F5);
    border: 1px solid #E8F1F5;
    padding:5px;
    font-size: 18px;
}
.active{
    color:#005691;
}
.item {
    display: flex;
    padding-right: 5px;
    padding-left:5px;
    margin-bottom: 2px;
}
.item > img {
    margin-right: 5px;
}
.item > img{
    width: 20px;
}