.avatar{
    position: relative;
    width: fit-content;
    height: fit-content;
    margin-right: 20px;
}
.avatar img{
    max-width:220px;
}
.avatar:hover > .avatarSettings{
    transform: scaleY(1);
}
.avatarSettings{
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    width: 100%;
    bottom: 4px;
    height: 50px;
    background-color: black;
    opacity: 0.5;
    transform: scaleY(0);
    transform-origin: 100% 100%;
    transition: 0.5s all ease-out;
}

.avatarSettings input {
    display: none;
}
.uploadFile > label{
    height:30px;
    width: 30px;
    background-image: url('../../../../assests/icons/uploadIcon.svg');
    background-size: cover;
    display: inline-flex;
    cursor: pointer;
}
