html{
    background-color: whitesmoke;
}
*{
    padding:0;
    margin:0;
    text-decoration: none;
    outline: none;
    color:black;
    box-sizing: border-box;
    font-size: 15px;
}
.App {
    margin: 0 auto;
    max-width: 1200px;
    display: grid;
    grid-gap: 10px;
    grid-template-areas:"h h"
    "n c";
    grid-template-rows: 50px auto;
    grid-template-columns: 1fr 10fr;
}

.content {
    grid-area: c;
    background: linear-gradient(to bottom, #FAFAFA, #E8F1F5);
    border:1px solid #E8F1F5;
    border-radius: 2px;
}