.avatar img {
    top: 0;
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.avatar {
    position: relative;
    width: 70px;
    height: auto;
    margin-right: 10px;
}

.user {
    padding:5px;
    display: flex;
    margin-bottom: 5px;
    box-shadow: 0 0 4px #005691;
    width: fit-content;
}

.user h2 {
    align-self: flex-start;
}
.name{
    font-weight: 500;
}