.input{
    min-width: 30%;
}
.textarea{
    width:100%;
}
.field{
    height: fit-content;
    resize: none;
    overflow: hidden;
    background-color: white;
    border: 2px solid #E8F1F5;
    padding-right: 20px;
}
.formField{
    position: relative;
    margin-bottom: 10px;
    width:100%;
    display: flex;
}
.errorIcon{
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 13px;
    font-weight: bold;
    position: absolute;
    width:15px;
    height:15px;
    background-color: red;
    right: 5px;
    border-radius: 50%;
    color:white;
    top:3px;
    cursor: pointer;
}
.errorIcon:hover + .errorMessage{
    transform: scale(1);
    opacity: 1;
}
.errorMessage{
    position: absolute;
    transform: scale(0);
    opacity: 0;
    background-color: red;
    color:white;
    width: fit-content;
    margin-left: 5px;
    padding:2px;
    border-radius: 10px;
    transition: 0.3s all ease-in;
    transform-origin: left;
}
.errorMessage:before{
    content:'';
    position: absolute;
    top:5px;
    border:7px solid transparent;
    border-right-color: red;
    left:-13px;

}
.label{
    min-width:100px;
     white-space: nowrap;
    opacity: 0.7;
    font-weight: 600;
}
.content{
    display: flex;
    position: relative;
}