.header{
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    grid-area: h;
    background-color: #FAFAFA;
    border: 1px solid #E8F1F5;
    padding-left: 10px;
    padding-right: 10px;
}
.icon img{
    width:30px;
    border-radius: 50%;
}
.icon{
    align-self: center;
}