.main {
    display: flex;

    align-items: stretch;
}

.avatar img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
}

.avatar {
    display: flex;
    align-items: flex-end;
}

.me {
    flex-direction: row-reverse;
}

.message {
    margin: 3px;
    position: relative;
    padding: 5px;
    border: 1px solid transparent;
    text-align: center;
    border-radius: 5px;
    box-shadow: 0 0 10px #005691;
}

.notMe .message:after {
    content: '';
    position: absolute;
    border: 7px solid transparent;
    border-right-color: #FAFAFA;
    left: 0;
    top: 3px;
    margin-left: -15px;
}

.notMe .avatar img {
    margin-right: 4px;
}
.notMe .newMessage{
    transform-origin: left;

}
.me .newMessage{
    transform-origin: right;

}
.me .name{
    text-align: right;
}
.me .avatar img {
    margin-left: 4px;
}

.me .message:after {
    content: '';
    position: absolute;
    border: 7px solid transparent;
    border-left-color: #FAFAFA;
    right: 0;
    top: 3px;
    margin-right: -15px;
}

.newMessage {
    animation: scale 0.5s;
}

@keyframes scale {
    0% {
        transform: scale(0);
    }
    60% {
        transform: scale(1.1);
    }
    100% {
        transform: scale(1);
    }

}